body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
}

#g svg {
  /*transform: rotate(270deg);*/
}

header {
  width: 50%;
  max-width: 720px;
  margin: 25px;
  float: left;
  position: fixed;
}

header h1 {
  font-weight: bold;
  font-size: 3em;
  text-align: center;
  margin: 0;
}

header h2 {
  font-weight: normal;
  font-size: 2em;
  text-align: center;
  margin: 0;
}

section .section-header {
  width: 530px;
}

strong.male-gender {
  color: rgb(102, 169, 186);
}

strong.female-gender {
  color: rgb(247, 148, 29);
}

#intro {
  margin: 0 auto 25px;
  line-height: 24px;
  font-size: 14px;
}

#intro .date {
  font-size: 12px;
  text-transform: capitalize;
}

#byline {
  font-size: 12px;
  margin: 0 auto 30px;
}

#byline span {
  font-weight: bold;
}

#tooltip {
  width: 260px;
  height: 40px;
  display: none;
  position: fixed;
  background-color: #FFF;
  border: 1px solid #CCC;
  text-align: center;
  font-size: 12px;
  padding: 6px 8px;
  overflow: hidden;
}

#tooltip-inside {
  line-height: 1em;
  display: table-cell;
  vertical-align: middle;
}

#tooltip .time {
  font-weight: bold;
}

.viz-contents {

}

g.runner {
  cursor: pointer;
}

#container-scale text {
  fill: #666;
}

#container-scale line {
  stroke: #666;
}

path {
  fill: none;
}

#container-notes line, #container-notes path {
  stroke: #666;
  stroke-width: 1px;
}

#container-notes text {
  fill: #666;
}

#legend {
  position: fixed;
  right: 5%;
  top: 20px;
}

#legend h3 {
  margin: 0;
}

#legend p {
  margin: 0;
}

.legend-item span.legend-text {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 12px;
}

.legend-item span.legend-color {
  -webkit-border-radius: 1.2em;
  -moz-border-radius: 1.2em;
  width: 12px;
  height: 12px;
  display: inline-block;
  margin: 0 4px -2px 0;
  white-space: nowrap;
}


